@import '../../../public/styles/main-styles.scss';

.header {
  height: 80px;
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 5fr 3fr;
  align-items: center;
  font-weight: bold;
  font-size: 20px;
  padding: 0 10%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);

  @media (max-width: 600px) {
    padding: 0 10px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__schedule {
    font-size: 14px;
    color: $darkGrey;
  }

  &__phone {
    margin-bottom: 10px;
  }

  &__logo {
    height: 100%;
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    width: 100%;

    @media (max-width: 1440px) {
      display: none;
    }
  }

  &__title {
    margin: 0;
    color: black;

    @media (max-width: 600px) {
      font-size: 20px;
    }
  }

  &__link {
    color: black;

    &-container {
      display: grid;
      grid-auto-flow: column;
      grid-gap: 16px;
      justify-content: center;
      align-items: center;
      list-style-type: none;
      padding: 0;

      @media (max-width: 1228px) {
        padding-left: 20px;
      }
    }
  }

  &__link > a {
    color: black;
  }
}


